body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif}button{background:none;border:none;padding:0;outline:none}

.app{display:flex;align-items:center;justify-content:center;height:100vh}

.switcher{display:grid;grid-template-columns:repeat(2, 1fr);justify-content:center;align-items:center;border:2px solid #e7e7e7;border-radius:26px;padding:2px;position:relative}.btn{cursor:pointer;min-width:150px;white-space:nowrap;padding:10px 14px;text-transform:uppercase;font-weight:600;color:#078b1f}.btn.off{color:#ff0000}.slider{position:absolute;left:2px;border-radius:28px;top:2px;bottom:2px;width:50%;z-index:-1;background-color:#44ce6f;transition:left .3s ease, background-color .3s ease;opacity:.7}.slider.inactive{background-color:rgba(179,21,21,0.5);left:50%}

